
import Swiper from 'vux/src/components/swiper/swiper.vue'
import SwiperItem from 'vux/src/components/swiper/swiper-item.vue'
import Tab from 'vux/src/components/tab/tab.vue'
import TabItem from 'vux/src/components/tab/tab-item.vue'

import rankList from 'components/rankList/index'
import http from 'js/http'

export default {
name: "home",
components: {
Swiper, SwiperItem, Tab, TabItem,
rankList
},
data() {
return {
swiperList: [],
nodeTab: [],
currentNodeId: '',
nodeList: [],
loadShow: true,
downTime: 0,
countDownIsOpen: false,
timeInterval: '',
}
},
methods: {
goNoticeDts(item) {
http.post('/notice/info', {
id: item.id
}, (res) => {
console.log(res)
if (res.code !== 0) {
this.$vux.toast.show(res.msg)
return
}
if (res.content.type == 0) {
window.location.href = res.content.url
} else {
localStorage.setItem("noticeInfo", JSON.stringify(res.content));
this.$router.push({
path: `/home/notice/dts${item.id}`
})
}

})
},
selectTab(index) {
this.currentNodeId = index
this.nodeList = []
this.getNodeList()
},
getNoticeList() {
http.post('/app/notice', {}, (res) => {
if (res.code !== 0) {
this.$vux.toast.show(res.msg)
return
}
this.swiperList = res.content
})
},
getNodeTab() {
http.post('/node', {}, (res) => {
if (res.code !== 0) {
this.$vux.toast.show(res.msg)
return
}
if (!res.content.length) {
this.loadShow = false
return
}
this.nodeTab = res.content
this.currentNodeId = res.content[0].id
this.getNodeList()
})
},
getNodeList() {
this.loadShow = true
http.post('/node/vote', {
id: this.currentNodeId
}, (res) => {
this.loadShow = false
if (res.code !== 0) {
this.$vux.toast.show(res.msg)
return
}
this.nodeList = res.content
})
},
pageInt() {
this.swiperList = []
this.nodeList = []
this.nodeTab = []
this.currentNodeId = ''
this.getCountTime()
this.getNoticeList()
this.getNodeTab()
},
getCountTime() {
http.post('/vote/count-time', {}, (res) => {
if (res.code !== 0) {
this.$vux.toast.show(res.msg)
return
}
this.downTime = res.content.downTime
this.countDownIsOpen = res.content.countDownIsOpen
if (this.timeInterval) clearInterval(this.timeInterval)
this.timeInterval = setInterval(() => {
if (this.downTime) {
this.downTime--
} else {
clearInterval(this.timeInterval)
}
}, 1000)
})
}
},
created() {
this.pageInt()
},
activated() {

},
computed: {
downTimeStr() {
if (!this.downTime) return '本轮竞选已结束'
const px_d = 60 * 60 * 24
const px_h = 60 * 60
const px_m = 60
const px_s = 1
let d = Math.floor(this.downTime / px_d);
let h = Math.floor((this.downTime - d * px_d) / px_h)
let m = Math.floor((this.downTime - d * px_d - h * px_h) / px_m)
let s = Math.floor((this.downTime - d * px_d - h * px_h - m * px_m) / px_s)
let r = []
if (d > 0) {
r.push(`${d}天`)
}
if (r.length || (h > 0)) {
r.push(`${h}时`)
}
if (r.length || (m > 0)) {
r.push(`${m}分`)
}
if (r.length || (s > 0)) {
r.push(`${s}秒`)
}
return r.join('')
}
},
watch: {
'$route': function (t, f) {
if (t.path === '/home') {

this.getNoticeList()
this.getNodeList()
}
}
}
}



